import axios from 'axios'
export default {
  asyncData(context) {
    return axios
      .get(
        'https://superhero-blog.firebaseio.com/posts/' +
          context.params.id +
          '.json'
      )
      .then((res) => {
        return {
          loadedPost: res.data,
        }
      })
      .catch((e) => context.error(e))
  },
}
